import React from 'react';
import Body from '../../../components/Body';

const AboveTheFold = () => (
  <div className="h-screen bg-primary bg-opacity-25 relative overflow-hidden">
    <Body>
      <div className="absolute h-1/2 inset-y-1/4 sm:p-0 w-full sm:w-5/6 lg:w-4/6">
        <div className="w-3/4 h-full">
          <Text />
        </div>
      </div>
    </Body>
    <BottomCut />
  </div>
);

const BottomCut = () => (
  <div className="absolute bottom-0">
    <div className="cut"></div>
  </div>
);

const Text = () => (
  <div>
    <div>
      <span className="typography-title">&gt; Hack Your Own</span>
    </div>
    <div className="h-16" />
    <div>
      <span className="typography-subtitle">
        Join us and start your internship with mentorship from industry
        professionals!
      </span>
    </div>
    <div className="h-8" />
    <button className="bg-secondary text-white py-3 px-4 rounded-lg shadow-custom">
      Sign Up
    </button>
  </div>
);

export default AboveTheFold;
